import React, { Suspense } from "react";
const Author = React.lazy(() => import("../../Elements/Author/Author"));
const Time = React.lazy(() => import("../../Elements/Time/Time"));
const Network = React.lazy(() => import("../../Elements/Network/Network"));

const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font, ownerId }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorNameStyle = { color: ThemeRule.authorColor };
  const sepratorStyle = { backgroundColor: ThemeRule.authorColor };
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return <div className="tb_sp_author_wrapper">
    <div className="tb_sp_author">
      {postAuthor || ownerId === 100231 ? <Suspense fallback={<></>}><Author author={author} network={network} authorClass={'tb_sp_author_profile'} /></Suspense> : ''}
      <div className="tb_sp_author_info">
        {postAuthor || ownerId === 100231 ? <div className="tb_sp_authorname" style={authorNameStyle}>{author.name}</div> : ''}
        <div className="tb_sp_post_info">
          {postAuthor || ownerId === 100231 ? <div className="tb_sp_username" style={authorNameStyle}>{username}</div> : ''}
          {authorSeprator ? <div className="tb_sp_seprator" style={sepratorStyle}> </div> : ''}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={'tb_sp_time'} authorColor={ThemeRule.authorColor} /> </Suspense> : ''}
        </div>
      </div>
    </div>
    <div className="tb_sp_social_">
      <Suspense fallback={<></>}><Network networkClass={'tb_sp_social__ico'} network={network} ThemeRule={ThemeRule} font={font} /></Suspense>
    </div>
  </div>
}
export default AuthorDetails